	module.exports = {"data":{"markdownRemark":{"html":"<h2>Integration Hell</h2>\n<p>Integration hell refers to the practice of integrating the different components of a project at the end of the project. The idea was that the specifications would completely describe the ways the components could interact, and that integration would be quick and easy.</p>\n<p>In practice, the longer components are developed in isolation, the more the interfaces tend to diverge from what is expected. When the components are finally integrated at the end of the project, it would take a great deal more time than allocated, often leading to deadline pressures, and difficult integration. This painful integration work at the end of the project is the eponymous hell.</p>\n<p><a href='https://guide.freecodecamp.org/agile/continuous-integration/'>Continuous Integration</a> is the Agile antidote to this problem. Integration is still painful, but doing it at least daily keeps interfaces from diverging too much.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://tobeagile.com/2017/03/08/avoiding-integration-hell/'>Avoiding Integration Hell</a></li>\n<li><a href='http://wiki.c2.com/?IntegrationHell'>Integration Hell</a></li>\n<li><a href='https://www.apicasystems.com/blog/top-5-tips-avoid-integration-hell-continuous-integration/'>Top 5 Tips to Avoid “Integration Hell” with Continuous Integration</a></li>\n</ul>","fields":{"slug":"/agile/integration-hell/"},"frontmatter":{"title":"Integration Hell"}}},"pathContext":{"slug":"/agile/integration-hell/"}}

